#contact{
    background-color: #1f2235;  
    padding-top: 5rem;
    padding-bottom: 5rem;
}

.adress{
    display: flex;
}

.adress i{
    color: #ff4a57;
    font-size: 1.6rem;

}

.adress  .adresscontent{
    margin-left: 20px;
    font-size: 1.1rem;
}

.adress .adresscontent span{
    font-weight: bold;
}

.adress .adresscontent p{
    padding: 0;
    margin: 0;
}


.adressArea .adress{
    padding:25px;
    background: #24263b;
}

.input-area, .text-area, .yourName, .yourMail{
    width: 100%;
    border: none;
    background-color: #24263b;
    margin-bottom: 20px;
    padding:  20px 35px;
    border-radius: 8px;
    color: #fff;
    outline: none;
}

.input-area:focus, .text-area:focus, .yourName:focus, .yourMail:focus{
    outline-style: solid;
    outline-color: #ff4a57;
}

::placeholder{
    color: #fff;
    opacity: 1;
}

.submitBtn{
    display: inline-block;
    cursor: pointer;
    border: none;
    background-color: #ff4a57;
    color: #fff;
    padding: 16px  40px;
    border-radius:  30px;
    font-weight: bold;
    transition: .7s ease ;
    font-size: 1rem;

}

.submitBtn:hover{
    color: #ff4a57;
    background-color: #fff;
    outline: none;
}




